* {
  box-sizing: border-box;
}

body {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background: url("http://i.hizliresim.com/v4Qykv.png") center / cover no-repeat fixed;
  font-family: Roboto, Tahoma, Arial, sans-serif;
  font-size: 13px;
  line-height: 1.5;
}

form {
  border-radius: 4px;
  outline: 0;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
  display: flex;
}

form > input {
  height: 42px;
  width: 244px;
  color: #212121;
  float: left;
  background-color: rgba(255, 255, 255, .8);
  border: 0;
  border-radius: 4px 0 0 4px;
  outline: 0;
  padding: 0 16px;
  line-height: 42px;
}

form > input:focus {
  background-color: #fff;
  outline: 0;
}

form > button {
  height: 42px;
  width: 62px;
  text-align: center;
  color: #fff;
  text-rendering: auto;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  background-color: rgba(38, 50, 56, .8);
  border: 0;
  border-radius: 0 4px 4px 0;
  font: 16px / 1 FontAwesome;
  transition: background-color .4s;
}

form > button:hover {
  background-color: rgba(0, 150, 136, .8);
}

.load-more {
  visibility: hidden;
  width: 100px;
  height: 40px;
  color: #fff;
  text-rendering: auto;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  background-color: rgba(38, 50, 56, .8);
  border: 0;
  border-radius: 4px;
  margin: 0 auto;
  font: 16px / 1 FontAwesome;
  transition: background-color .4s;
  display: block;
}

.load-more.visible {
  visibility: visible;
}

.load-more.visible:hover {
  background-color: rgba(0, 150, 136, .8);
}

.gallery {
  flex-flow: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.photo-card {
  background-color: rgba(89, 170, 104, .6);
  border: 1px solid #000;
  border-radius: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.photo-card:hover {
  transform: scale(1.05);
}

.gallery__image {
  max-width: 100%;
  display: block;
}

.info {
  padding: 0 20px;
}

.info-item {
  font-size: 15px;
  line-height: .7;
}

.info-other {
  flex-flow: wrap;
  gap: 10px;
  display: flex;
}

#back-to-top {
  width: 30px;
  height: 30px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background-color: #212121;
  border-radius: 4px;
  transition: background-color .3s, opacity .5s, visibility .5s;
  display: inline-block;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

@media screen and (min-width: 768px) {
  #back-to-top {
    width: 50px;
    height: 50px;
    bottom: 30px;
    right: 30px;
  }
}

#back-to-top:after {
  content: "";
  color: #fff;
  font-family: FontAwesome;
  font-size: 1em;
  font-style: normal;
  font-weight: normal;
  line-height: 30px;
}

@media screen and (min-width: 768px) {
  #back-to-top:after {
    font-size: 2em;
    line-height: 50px;
  }
}

#back-to-top:hover {
  cursor: pointer;
  background-color: rgba(0, 150, 136, .8);
}

#back-to-top:active {
  background-color: #555;
}

#back-to-top.show {
  opacity: 1;
  visibility: visible;
}

